.hmbrgr-btn__base, .hmbrgr-btn__base::before, .hmbrgr-btn__base::after {
  width: 20px;
  height: 3px;
  border-radius: 2px;
  background-color: #dadada;
  -webkit-transition: all ease-in 0.5s;
  transition: all ease-in 0.5s;
}
.hmbrgr-btn__base::before, .hmbrgr-btn__base::after {
  content: "";
  position: absolute;
}
.hmbrgr-btn__base::before {
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
}
.hmbrgr-btn__base::after {
  -webkit-transform: translateY(5px);
  transform: translateY(5px);
}
.hmbrgr-btn[aria-expanded=true] > .hmbrgr-btn__base {
  background: transparent;
}
.hmbrgr-btn[aria-expanded=true] > .hmbrgr-btn__base::before {
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}
